import { BASE_IMAGE_URL,getOrders} from "@/utils/api";
import {isWeiXin} from "@/utils/index.js"

export default {
  data(){
	  return{
		  currentTab: 0,
		  list: [],
		  dai_pay_icon: BASE_IMAGE_URL+"dai_pay_icon.png",
		  finish_icon: BASE_IMAGE_URL+"finish_icon.png",
			baozhangzhong_icon: BASE_IMAGE_URL+"baozhangzhong_icon.png",
    weiqibao_icon: BASE_IMAGE_URL+"weiqibao_icon.png",
    yidaoqi_icon: BASE_IMAGE_URL+"yidaoqi_icon.png",
	  }
    
  },

  methods: {
    changeType: function(index){
      console.log(index);
      this.currentTab = index;
			window.scrollTo(0,0);
      this.getOrders();
    },
    async getOrders(){
			this.list = [];
      let res = await getOrders();
      if(res.code == 200){
				if(this.currentTab == 0){
					res.data.map(data => {
						if(data.type == 1 || data.type == 2){
							this.list.push(data);
						}
					})
				}else if(this.currentTab == 1){
					res.data.map(data => {
						if(data.type == 1 || data.type == 2){
							if(data.status == 1){
								this.list.push(data);
							}
						}
					})
				}else{
					res.data.map(data => {
						if(data.type == 1 || data.type == 2){
							if(data.status >= 3){
								this.list.push(data);
							}
						}
					})
				}
        // this.list = res.data;
      }
    },

    next: function(item){
      let index = item.type;
      if(item.status == 1){
        //未付款
        if(index == 1){
          //里程车险
     //      const url = "../pay_address/pay_address?quotation_id="+item.quotation_id;
     //      wx.navigateTo({ url });
					// return;
					
					let openid = uni.getStorageSync("openid");
					console.log(openid);
					
					if(!isWeiXin()){
						uni.removeStorageSync("openid");
					}
				
					if(!openid){
						if(isWeiXin()){
							//如果是微信浏览器 去获取openid
							let partner_id = uni.getStorageSync("partner_id");
							let imei = uni.getStorageSync("imei");
							window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx49aad3a138063b53&redirect_uri=https://api.kaikaibao.com.cn/3.1/getoauth?redit_url=https%3a%2f%2fm.kaikaibao.com.cn%2flccx2%2findex.html%23%2fpages%2fpay_address%2fpay_address%3fquotation_id%3d"+item.quotation_id+"%26partner_id%3d"+partner_id+"%26imei%3d"+imei+"&response_type=code&scope=snsapi_base&state=1&connect_redirect=1#wechat_redirect";
						}else{
							const url = '../pay_address/pay_address?quotation_id='+item.quotation_id;
							uni.navigateTo({ url })
						}
					}else{
						const url = '../pay_address/pay_address?quotation_id='+item.quotation_id;
						uni.navigateTo({ url })
					}
			
        }else{
          //交强险
          wx.showModal({
            title: '提示',
            content: '请先支付里程车险，再支付交强险,现在为您跳转',
            success: function (res) {
                if (res.cancel) {
                  //点击取消,默认隐藏弹框
                } else {
                  //点击确定
									let openid = uni.getStorageSync("openid");
									console.log(openid);
									
									if(!isWeiXin()){
										uni.removeStorageSync("openid");
									}
                  if(!openid){
											if(isWeiXin()){
												//如果是微信浏览器 去获取openid
												let partner_id = uni.getStorageSync("partner_id");
												let imei = uni.getStorageSync("imei");
												window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx49aad3a138063b53&redirect_uri=https://api.kaikaibao.com.cn/3.1/getoauth?redit_url=https%3a%2f%2fm.kaikaibao.com.cn%2flccx2%2findex.html%23%2fpages%2fpay_address%2fpay_address%3fquotation_id%3d"+item.quotation_id+"%26partner_id%3d"+partner_id+"%26imei%3d"+imei+"&response_type=code&scope=snsapi_base&state=1&connect_redirect=1#wechat_redirect";
											}else{
												const url = '../pay_address/pay_address?quotation_id='+item.quotation_id;
												uni.navigateTo({ url })
											}
										}else{
											const url = '../pay_address/pay_address?quotation_id='+item.quotation_id;
											uni.navigateTo({ url })
										}
                }
            }
          });
        }

      }else{
        //已付款
        if(index == 1){
          //里程车险
          const url = "../order_one_detail/order_one_detail?id="+item.id;
          wx.navigateTo({ url });
        }else{
          //交强险
          const url = "../order_two_detail/order_two_detail?id="+item.id;
          wx.navigateTo({ url });

        }

        
      }
    }

  },

  onLoad () {
    this.getOrders();
  },


}
